<template>
  <div id="check">
      <div style="text-align:center;">
          <Table :loading="loading" :columns="checkKey" :data="checkData" stripe></Table> 
          <Page :total="checkData.length" class="page" v-show="checkData.length"></Page>                  
      </div> 
  </div>
</template>

<script>
export default {
  name: 'check',
  data () {
    return {
      loading: false,
      checkKey: [
          {
              title: '姓名',
              key: 'username',
              render: (h, params) => {
                  return h('div', [
                      h('Icon', {
                          props: {
                              type: 'person'
                          }
                      }),
                      h('strong', params.row.username)
                  ]);
              }
          },
          {
              title: '学号',
              key: 'student_num'
          },
          {
                  title: '操作',
                  key: 'action',
                  width: 150,
                  align: 'center',
                  render: (h, params) => {
                      return h('div', [   
                          h('Button', {
                              props: {
                                type: 'success',
                                size: 'small'
                              },
                              style: {
                                marginRight: '5px'
                              },
                              on: {
                                click: () => {
                                  this.agree(this.checkData[params.index].student_num,params.index);
                                }
                              }
                          }, '同意'),                             
                          h('Button', {
                              props: {
                                  type: 'error',
                                  size: 'small'
                              },
                              on: {
                                  click: () => {                                   
                                    const content = '<p>确定删除吗？</p>';
                                    this.$Modal.warning({                                        
                                        content: content,
                                        onOk: () => {                                          
                                          this.refuseUser(this.checkData[params.index].student_num,params.index);
                                        }
                                    })
                                 }
                              }
                          }, '拒绝')
                      ]);
                  }
              }
      ],
      checkData: []      
    } 
  },
  created(){
    this.$http.get('/user/unCheckUser')
      .then(res => {
        this.checkData = res.data.msg;
      }).catch(err => {

      })
  },
  methods:{
    refuseUser(student_num,index){
      this.$http.post('/user/refuseUser',{
        'student_num': student_num
      }).then(res => {
        this.checkData.splice(index,1);
        this.$Message.info('删除成功！');
      })
    },
    agree(student_num,index){
      this.$http.post('/user/agreeUser',{
        'student_num': student_num
      }).then(res => {
        this.checkData.splice(index,1);
        this.$Message.info('操作成功！');
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.page{
  margin-top: 10px;
}
</style>



